.more {
  width: 100%;

  background: linear-gradient(to bottom, #131314 87.7%, #ffffff 87.7%);

  @media (max-width: $tablet-width-max) {
    width: 100%;
    min-height: 498px;

    background: linear-gradient(to bottom, #131314 82.93%, #ffffff 82.93%);
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 384px;

    background: #131314;
  }
}

.more__wrapper {
  max-width: 1440px;
  height: 100%;

  text-align: center;
  letter-spacing: -0.01em;

  position: relative;

  color: #ffffff;

  padding: 0;
  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    padding: 0;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    padding: 0;
  }
}

.more h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;

  padding: 122px 0 0;
  margin: 0 0 20px;

  @media (max-width: $tablet-width-max) {
    font-size: 24px;

    padding: 70px 0 0;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 24px;

    padding: 85px 0 0;
  }
}

.more p {
  width: 100%;
  max-width: 340px;

  line-height: 28px;
  font-weight: 300;

  opacity: 70%;

  margin: 0 auto 55px;

  @media (max-width: $tablet-width-max) {
    width: 55.86%;
    max-width: none;

    margin: 0 auto 40px;
  }

  @media (max-width: $mobile-width-max) {
    width: 90%;

    margin: 0 auto 0;
  }
}

.more__video-container {
  width: 100%;
  max-width: 929px;

  display: block;

  position: relative;

  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 65.49%;

    display: block;

    margin: 0 auto;
  }

  @media (max-width: $mobile-width-max) {
    width: calc(100% - 32px);
    min-height: 162px;

    display: block;

    top: 52px;

    margin: 0 auto;
  }
}

.more video {
  width: 100%;

  display: block;

  background: url("../img/video-preview.png") no-repeat;
  background-size: 100% 522px;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: 100%;
    min-height: 282px;

    display: block;

    background-size: 100% 100%;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 100%;
    min-height: 162px;

    display: block;

    background-size: 100% 100%;
  }
}

.more button {
  width: 65px;
  height: 65px;

  position: absolute;
  top: 229px;
  left: 50%;
  transform: translateX(-50%);

  border: 0;

  background: transparent;

  padding: 0;

  cursor: pointer;

  @media (max-width: $tablet-width-max) {
    width: 90.36px;
    height: 90.36px;

    top: 50%;
    transform: translate(-50%, -50%);
  }

  @media (max-width: $mobile-width-max) {
    width: 65px;
    height: 65px;

    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.more button:hover .triangle {
  fill: #ffffff;
}

.more button:focus {
  outline: 3px solid rgb(0, 191, 255);
}

.more svg {
  width: 65px;
  height: 65px;

  @media (max-width: $tablet-width-max) {
    width: 90.36px;
    height: 90.36px;
  }

  @media (max-width: $mobile-width-max) {
    width: 65px;
    height: 65px;
  }
}
