.callback {
  width: 37.16%;
  min-width: 270px;

  order: 3;

  padding: 260px 0 0;

  @media (max-width: $tablet-width-max) {
    width: 80%;
    min-width: none;
    height: max-content;

    padding: 0;
    margin: 0 auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    min-width: none;
    height: max-content;

    padding: 70px 0 0;
  }
}

.callback form {
  width: 100%;

  height: 100%;

  display: flex;
  flex-flow: column wrap;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: auto;

    padding: 0;
  }

  @media (max-width: $mobile-width-max) {
    width: 90%;
    height: auto;

    padding: 0;
    margin: 0 auto;
  }
}

.callback p {
  width: 100%;
  height: max-content;

  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  text-align: center;

  margin: 0 0 20px;

  @media (min-width: 768px) and (max-width: $tablet-width-max) {
    width: 52.25%;

    font-size: 18px;
    color: #000000;

    margin: 0 auto 30px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 18px;
    color: #000000;

    margin: 0 0 20px;
  }
}

.callback label {
  width: 100%;
  max-width: 327px;

  margin: 0 auto 20px;

  @media (max-width: $tablet-width-max) {
    width: 52.25%;

    margin: 0 auto 20px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    max-width: none;

    margin: 0 0 20px;
  }
}

.callback input {
  width: 100%;
  height: 48px;

  font-family: inherit;
  font-size: 15px;
  line-height: 24px;

  color: rgb(28, 28, 28);

  border: 1px solid #c9ccd4;
  border-radius: 6px;

  padding: 1px 0 0 30px;
  margin: 0;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    padding: 1px 0 0 21px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    padding: 1px 0 0 23px;
  }
}

.callback input:focus {
  outline: 3px solid rgb(0, 255, 55);
}

.callback__submit {
  @media (max-width: $mobile-width-max) {
    width: 100%;
  }
}

.callback__submit button {
  width: 100%;
  max-width: 325.13px;
  height: auto;
  min-height: 51px;

  font-family: inherit;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;

  display: block;

  color: rgb(0, 0, 0);
  background: rgba(10, 217, 198, 100%);

  border: none;
  border-radius: 6px;

  padding: 0;
  margin: 0 auto;

  cursor: pointer;

  @media (max-width: $tablet-width-max) {
    width: 322px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    max-width: none;

    margin: 0;
  }
}

.callback__submit button:hover {
  opacity: 80%;
}
