.header {
  width: 100%;
  min-height: 825px;

  background: url("../img/header-bicycle-desktop.png") no-repeat, linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
  background-size: 100% 100%;

  @media (min-resolution: 2dppx) {
    background: url("../img/header-bicycle-desktop@2x.png"), linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
    background-size: 100% 100%;
  }

  @media (max-width: $tablet-width-max) {
    width: 100%;
    min-height: 683px;

    background: url("../img/header-bicycle-tablet.png") no-repeat, linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
    background-size: 100% 340px;

    @media (min-resolution: 2dppx) {
      background: url("../img/header-bicycle-tablet@2x.png") no-repeat, linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
      background-size: 100% 340px;
    }
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 750px;

    background: url("../img/header-bicycle-mobile.png") no-repeat, linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
    background-size: 100% 390px;

    @media (min-resolution: 2dppx) {
      background: url("../img/header-bicycle-mobile@2x.png") no-repeat, linear-gradient(#808080 100%, #ffffff 0%) no-repeat;
      background-size: 100% 390px;
    }
  }
}

.header__wrapper {
  width: 61.11%;
  height: 100%;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  position: relative;

  color: $header-text-color;

  padding: 21px 0 240px;
  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    align-items: center;

    padding: 0 0 70px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    text-align: center;

    padding: 0 0 70px;
  }
}

.header__logo-link {
  display: none;

  @media (max-width: $tablet-width-max) {
    width: 67.6px;
    height: 20.28px;

    display: inherit;

    margin: 19px 0 0 21px;
  }

  @media (max-width: $mobile-width-max) {
    width: 67.6px;
    height: 20.28px;

    display: inherit;

    margin: 21px 0 0 16px;
  }
}

.header__logo {
  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: auto;
  }
}

.header__button {
  display: none;

  @media (max-width: $tablet-width-max) {
    width: 50px;
    height: 50px;

    display: block;

    border: none;

    background: #0ad9c6;

    padding: 0;
  }

  @media (max-width: $mobile-width-max) {
    width: 50px;
    height: 50px;

    display: block;

    border: none;

    background: #0ad9c6;

    padding: 0;
  }
}

.header__open {
  @media (max-width: $tablet-width-max) {
    width: 30px;
    height: 30px;

    margin: auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 30px;
    height: 30px;

    margin: auto;
  }
}

.header__close {
  @media (max-width: $tablet-width-max) {
    width: 22px;
    height: 22.72px;
  }

  @media (max-width: $mobile-width-max) {
    width: 22px;
    height: 22.72px;
  }
}

.header--no-js .header__logo {
  display: none;

  @media (max-width: $tablet-width-max) {
    width: 67.6px;
    height: 40.56px;

    display: block;

    margin: auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 67.6px;
    height: 40.56px;

    display: block;

    margin: auto;
  }
}

.header--no-js button {
  @media (max-width: $tablet-width-max) {
    display: none;
  }

  @media (max-width: $mobile-width-max) {
    display: none;
  }
}

.header--menu-open .header__button {
  @media (max-width: $tablet-width-max) {
    width: 50px;
    height: 52px;

    display: block;

    position: absolute;
    top: 0;
    right: 0;

    z-index: 2;
  }

  @media (max-width: $mobile-width-max) {
    width: 50px;
    height: 52px;

    display: block;

    position: absolute;
    top: 0;
    right: 0;

    z-index: 2;
  }
}
