.contacts {
  width: 100%;

  text-align: center;
}

.contacts h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;

  color: #1f1f1f;

  margin: 0 0 30px;

  @media (max-width: $tablet-width-max) {
    font-size: 24px;

    margin: 0 0 20px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 18px;

    margin: 0 0 20px;
  }
}

.contacts p {
  width: 57.99%;

  font-size: 17px;
  line-height: 31px;

  color: rgba(0, 0, 0, 70%);

  margin: 0 auto 42px;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    margin: 0 0 20px;
  }

  @media (max-width: $mobile-width-max) {
    width: 80%;

    margin: 0 auto 20px;
  }
}

.contacts a {
  color: rgba(0, 0, 0, 50%);
}

.contacts iframe {
  width: 100%;
  height: 530px;

  border: none;

  @media (max-width: $tablet-width-max) {
    width: 90.89%;
    height: 545px;

    margin: 0 auto 5px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 250px;

    margin: 0 auto 14px;
  }
}

.contacts__data-wrapper {
  width: 100%;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  margin: 22px 0 0;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    display: flex;
    justify-content: space-between;

    margin: 1px 0 0;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    flex-flow: row wrap;

    margin: 0;
  }
}

.contacts__email-container {
  width: 50%;

  display: flex;
  justify-content: flex-end;

  @media (max-width: $mobile-width-max) {
    width: 100%;

    justify-content: center;

    padding: 0 0 0 8px;
    margin: 0 0 18px;
  }
}

.contacts__email {
  line-height: 21.79px;

  position: relative;

  padding: 7px 12px 0 41px;

  &::before {
    content: "";

    width: 26px;
    height: 20px;

    position: absolute;
    top: 7px;
    left: 0;

    background: url("../img/mail.svg");
  }

  @media (max-width: $tablet-width-max) {
    padding: 7px 8px 0 41px;
  }

  @media (max-width: $mobile-width-max) {
    padding: 6px 0 0 36px;

    &::before {
      top: 7px;
      left: 0;
    }
  }
}

.contacts__email:hover {
  color: rgb(255, 166, 0);
}

.contacts__email:focus {
  outline: 3px solid rgb(0, 191, 255);
}

.contacts__phone-container {
  width: 50%;

  display: flex;

  padding: 0 0 0 75px;

  @media (max-width: $tablet-width-max) {
    padding: 0 0 0 79px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    padding: 0 11px 0 0;
  }
}

.contacts__phone {
  line-height: 32px;
  letter-spacing: normal;

  position: relative;

  padding: 1px 0 0 37px;

  &::before {
    content: "";

    width: 21.5px;
    height: 22.62px;

    position: absolute;
    top: 3px;
    left: 0;

    background: url("../img/phone.svg");
  }

  @media (max-width: $mobile-width-max) {
    margin: 0 auto;
  }
}

.contacts__phone:hover {
  color: rgb(255, 166, 0);
}

.contacts__phone:focus {
  outline: 3px solid rgb(0, 191, 255);
}
