.about {
  width: 44.66%;
  min-width: 298px;

  display: flex;
  flex-flow: row wrap;
  order: 2;

  padding: 256px 0 0;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    flex-flow: column;
    justify-content: center;
    align-items: center;

    padding: 84px 0 0;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    flex-flow: column;
    justify-content: center;
    align-items: center;

    padding: 70px 0;
  }
}

.about__logo-link {
  width: 17.2%;
  height: 20.28px;

  @media (max-width: $tablet-width-max) {
    display: none;
  }

  @media (max-width: $mobile-width-max) {
    display: none;
  }
}

.about__logo {
  width: 100%;
  height: auto;
}

.about h1 {
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;

  padding: 30.72px 0 0;
  margin: 0;

  @media (max-width: $tablet-width-max) {
    font-size: 30px;
    line-height: 42px;

    padding: 0;
    margin: 0 0 20px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 30px;
    line-height: 42px;

    padding: 0;
    margin: 0 0 20px;
  }
}

.about p {
  min-height: 96px;

  font-size: 20px;
  font-weight: 300;
  text-align: left;

  opacity: 80%;

  padding: 24px 0 0;
  margin: 0;

  @media (max-width: $tablet-width-max) {
    width: 56.64%;

    font-size: 18px;
    line-height: 32px;
    text-align: center;

    padding: 0;
    margin: 0 0 118px;
  }

  @media (max-width: $mobile-width-max) {
    width: 90%;

    font-size: 18px;
    line-height: 32px;
    text-align: center;

    padding: 0;
    margin: 0;
  }
}

.about--no-js {
  @media (max-width: $tablet-width-max) {
    color: #000000;
  }

  @media (max-width: $mobile-width-max) {
    color: #000000;
  }
}
