.navigation {
  width: 100%;
  height: max-content;

  order: 1;

  @media (max-width: $tablet-width-max) {
    display: none;
  }

  @media (max-width: $mobile-width-max) {
    display: none;
  }
}

.navigation ul {
  width: 100%;

  text-align: center;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  position: relative;

  color: $header-text-color;
  opacity: 70%;

  padding: 0 22px 9px;
}

.navigation ul::before {
  content: "";

  width: 100%;
  height: 1px;

  position: absolute;
  left: 0;
  bottom: 0;

  background: $navigation-bottom-line;
}

.navigation li:hover > a {
  color: rgb(243, 158, 1);
}

.navigation a:focus {
  outline: 3px solid rgb(0, 191, 255);
}

.navigation a {
  color: inherit;
}

.navigation--no-js {
  width: 100%;
  height: max-content;

  order: 1;

  @media (max-width: $tablet-width-max) {
    display: block;

    background: #ffffff;
  }

  @media (max-width: $mobile-width-max) {
    display: block;

    background: #ffffff;
  }
}

.navigation--no-js ul {
  width: 100%;

  text-align: center;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  position: relative;

  color: $header-text-color;
  opacity: 70%;

  padding: 0 22px 9px 19px;

  @media (max-width: $tablet-width-max) {
    min-height: 330px;

    display: block;

    padding: 48px 0 138px;
  }

  @media (max-width: $mobile-width-max) {
    min-height: 330px;

    display: block;

    padding: 48px 0 138px;
  }
}

.navigation--no-js li {
  width: max-content;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: 60px;

    display: flex;

    position: relative;

    align-items: center;

    background: #f6f6f6;

    padding: 0 0 0 16px;
    margin: 0 0 30px;

    &::before {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);

      background: url("../img/arrow-right.svg");
    }
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 60px;

    display: flex;

    position: relative;

    align-items: center;

    background: #f6f6f6;

    padding: 0 0 0 16px;
    margin: 0 0 30px;

    &::before {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);

      background: url("../img/arrow-right.svg");
    }
  }

  &:last-child {
    margin: 0;
  }
}

.navigation--no-js a {
  @media (max-width: $tablet-width-max) {
    width: 100%;

    text-align: start;

    display: block;

    color: #48494d;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    text-align: start;

    display: block;

    color: #48494d;
  }
}

.navigation--menu-open {
  width: 100%;
  height: 100vh;

  position: absolute;
  top: 48px;
  left: 0;

  order: 1;

  z-index: 2;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: 100vh;

    display: block;

    top: 0;
    left: 0;

    background: #ffffff;

    padding: 70px 0 138px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 100vh;

    display: block;

    position: absolute;
    top: 0;
    left: 0;

    background: #ffffff;

    padding: 148px 0 138px;
  }
}

.navigation--menu-open ul {
  width: 100%;

  text-align: center;

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  color: $header-text-color;
  opacity: 70%;

  padding: 0 22px 9px 19px;

  z-index: 2;

  @media (max-width: $tablet-width-max) {
    min-height: 330px;

    display: block;

    padding: 48px 0 138px;
  }

  @media (max-width: $mobile-width-max) {
    min-height: 330px;

    display: block;

    padding: 0;
  }
}

.navigation--menu-open li {
  width: max-content;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: 60px;

    display: flex;

    position: relative;

