      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);

      background: url("../img/arrow-right.svg");
    }
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 60px;

    display: flex;

    position: relative;

    align-items: center;

    background: #f6f6f6;

    padding: 0 0 0 16px;
    margin: 0 0 30px;

    &::before {
      content: "";

      width: 25px;
      height: 25px;

      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);

      background: url("../img/arrow-right.svg");
    }
  }

  &:last-child {
    margin: 0;
  }
}

.navigation--menu-open a {
  @media (max-width: $tablet-width-max) {
    width: 100%;

    text-align: start;

    display: block;

    color: #48494d;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    text-align: start;

    display: block;

    color: #48494d;
  }
}
