.kinds {
  width: 100%;

  text-align: center;

  background: #f6f7f9;

  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 100%;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
  }
}

.kinds div {
  width: 100%;
  max-width: 1440px;

  padding: 150px 0 148px;
  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    padding: 50px 0 70px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    padding: 50px 0 53px;
  }
}

.kinds h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;

  color: #1f1f1f;

  margin: 0 0 50px;

  @media (max-width: $tablet-width-max) {
    font-size: 24px;
    line-height: 41px;

    margin: 0 0 30px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 24px;
    line-height: 41px;

    margin: 0 0 30px;
  }
}

.kinds ul {
  width: 78.19%;
  min-width: 950px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 50px 50px;

  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 76.82%;
    min-width: 590px;

    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 49px 50px;

    margin: 0 auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 84.38%;
    min-width: 270px;

    display: grid;
    grid-template-columns: 1fr;
    gap: 43px 0;

    margin: 0 auto;
  }
}

.kinds li {
  width: 100%;
  max-width: 342px;

  position: relative;

  color: #000000;

  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 45.76%;
    max-width: 270px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    max-width: 270px;

    margin: 0 auto;
  }
}

.kinds li:nth-child(3n+1) {
  padding: 10px 0 0;

  &::before {
    content: "";

    width: 60px;
    height: 180px;

    position: absolute;
    top: 166px;
    left: -60px;

    background: url("../img/left-ellipse-desktop.svg");

    @media (max-width: $tablet-width-max) {
      width: 37px;
      height: 127px;

      top: 200px;
      left: -37px;

      background: url("../img/left-ellipse-tablet.svg") no-repeat;
    }

    @media (max-width: $mobile-width-max) {
      width: 24px;
      height: 127px;

      top: 202px;
      left: -25px;

      background: url("../img/left-ellipse-tablet.svg") no-repeat;
    }
  }

  &::after {
    @media (max-width: $tablet-width-max) {
      content: "";

      width: 127px;
      height: 28px;

      position: absolute;
      top: 299px;
      left: -37px;
      transform: rotate(180deg);

      background: url("../img/left-ellipse-tablet.svg") no-repeat;
    }

    @media (max-width: $mobile-width-max) {
      content: "";

      width: 127px;
      height: 28px;

      position: absolute;
      top: 301px;
      left: -25px;
      transform: rotate(180deg);

      background: url("../img/left-ellipse-tablet.svg") no-repeat;
    }
  }

  @media (max-width: $tablet-width-max) {
    padding: 0;
  }

  @media (max-width: $mobile-width-max) {
    padding: 0;
  }
}

.kinds li:nth-child(3n+2) {
  padding: 39px 0 0;

  ::before {
    content: "";

    width: 47px;
    height: 47px;

    position: absolute;
    top: 21px;
    left: -19px;

    background: url("../img/center-square.svg") no-repeat;

    @media (max-width: $tablet-width-max) {
      display: none;
    }

    @media (max-width: $mobile-width-max) {
      display: none;
    }
  }

  ::after {
    content: "";

    width: 47px;
    height: 47px;

    position: absolute;
    top: 581px;
    left: none;
    right: -23px;

    background: url("../img/center-square.svg") no-repeat;

    @media (max-width: $tablet-width-max) {
      display: none;
    }

    @media (max-width: $mobile-width-max) {
      display: none;
    }
  }

  @media (max-width: $tablet-width-max) {
    padding: 0;
  }

  @media (max-width: $mobile-width-max) {
    padding: 0;
  }
}

.kinds li:nth-child(3n+3) {
  ::before {
    content: "";

    width: 95px;
    height: 95px;

    position: absolute;
    top: -32px;
    right: -36px;

    background: url("../img/right-ellipse-desktop.svg") no-repeat;

    @media (max-width: $tablet-width-max) {
      width: 68px;
      height: 68px;

      top: -20px;
      right: -25px;

      background: url("../img/right-ellipse-tablet.svg") no-repeat;
    }

    @media (max-width: $mobile-width-max) {
      width: 68px;
      height: 68px;

      top: -21px;
      right: -15px;

      background: url("../img/right-ellipse-tablet.svg") no-repeat;
    }
  }
}

.kinds picture,
.kinds img {
  width: 100%;
  height: 503px;

  @media (max-width: $tablet-width-max) {
    width: 100%;
    height: 300px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    height: 300px;
  }
}

.kinds li:nth-child(3n+2) > picture,
.kinds li:nth-child(3n+2) img {
  height: 566px;

  @media (max-width: $tablet-width-max) {
    height: 300px;
  }

  @media (max-width: $mobile-width-max) {
    height: 300px;
  }
}

.kinds li:nth-child(3n+3) > picture,
.kinds li:nth-child(3n+3) img {
  height: 512.59px;

  @media (max-width: $tablet-width-max) {
    height: 300px;
  }

  @media (max-width: $mobile-width-max) {
    height: 300px;
  }
}

.kinds p {
  font-size: 22px;
  line-height: 34px;
  font-weight: 600;
  letter-spacing: -0.5px;
  margin: 10px 0 0;
}
