.evidence {
  width: 81.04%;
  max-width: 1440px;

  text-align: center;

  padding: 150px 0;
  margin: 0 auto;

  @media (max-width: $tablet-width-max) {
    width: 100%;

    padding: 70px 0 70px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    padding: 70px 0 78px;
  }
}

.evidence h2 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;

  color: #1f1f1f;

  margin: 0 0 50px;

  @media (max-width: $tablet-width-max) {
    font-size: 24px;
    line-height: 41px;

    margin: 0 0 30px;
  }

  @media (max-width: $mobile-width-max) {
    font-size: 24px;
    line-height: 41px;

    margin: 0 0 30px;
  }
}

.evidence ul {
  width: 100%;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 38px 38px;

  counter-reset: evidence-counter;

  @media (max-width: $tablet-width-max) {
    width: 72.4%;

    gap: 40px 30px;

    margin: 0 auto;
  }

  @media (max-width: $mobile-width-max) {
    width: 82.19%;

    gap: 35px 0;

    margin: 0 auto;
  }
}

.evidence li {
  width: 22.55%;

  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;

  color: #000000;

  position: relative;

  padding: 55px 0 0;

  @media (max-width: $tablet-width-max) {
    width: 47.3%;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;

    font-size: 18px;
  }
}

.evidence li::before {
  counter-increment: evidence-counter;
  content: "0" counter(evidence-counter);

  width: 100%;
  height: 55px;

  font-size: 28px;
  line-height: 38px;
  font-weight: 700;
  letter-spacing: -1.27px;

  position: absolute;
  top: 0;
  left: 0;

  color: #0ad9c6;
}
